import { Button, Card, Flex, Heading, Text, useModal } from '@pancakeswap/uikit'
import { useWeb3React } from '@web3-react/core'
import axios from 'axios'

import BigNumber from 'bignumber.js'
import Page from 'components/Layout/Page'
import PageHeader from 'components/PageHeader'
import nftpools from 'config/constants/nftpools'
import { useTranslation } from 'contexts/Localization'
import { useDNFTContract, useNftStakingContract } from 'hooks/useContract'
import useToast from 'hooks/useToast'
import React, { useEffect, useCallback, useState, useMemo, useRef } from 'react'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { getDNFTContract, getNftStakingContract } from 'utils/contractHelpers'
import StakingModal from './StakingModal'

const StyledCard = styled(Card)`
  align-self: center;
  display:block;
  width:360px;
  margin:10px;
`

const FarmCardInnerContainer = styled(Flex)`
  flex-direction: column;
  justify-content: space-around;
  
  padding: 24px;
`

const ExpandingWrapper = styled.div`
  padding: 24px;
  border-top: 2px solid ${({ theme }) => theme.colors.cardBorder};
  overflow: hidden;
`
class NFTModal{
	image="";

	description="";

	name="";

	id="";
}


const ViewNFTs: React.FC = () => {
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const hist=useHistory();
  const [mystate, setMystate] = useState<any>()
  const { toastError, toastSuccess } = useToast()
  const pid = new URLSearchParams(hist.location.search).get('pid');
  const wstakingcontract=useNftStakingContract(nftpools[pid].contractAddress[56]);


  console.log(pid)
  useEffect(()=>{
    async function loadData(){
      const contract = getDNFTContract(nftpools[pid].stakingToken.address[56]);
      const gmarketcontract = getNftStakingContract(nftpools[pid].contractAddress[56]);
      
        
      if(account)
      {

        const res=await gmarketcontract.getList(account)
        console.log("sad ",res)
        const alldata=[];
        for(let i=0;i<res.length;i++)
        {
          console.log("sads ",res)
            const uri=await contract.tokenURI(res[i].tokenId);
            console.log(uri)
            try{
              const resp= await axios.get("https://ipfs.io/ipfs/".concat("".concat(uri).substring(7)),{
                headers: {
                    'content-type': 'application/json'
                }
            });
            alldata.push(<StyledCard key={i} isActive={!false}>
              <FarmCardInnerContainer>
              <Flex justifyContent="space-between">
                  <Text>NFT Id:</Text>
                  <Text bold>{Number(res[i].tokenId)}</Text>
                </Flex>
                <br/>
                <Flex justifyContent="space-between">
                    <Text>Description:</Text>
                    <Text bold>{resp.data.description}</Text>
                  </Flex>
                  <br/>
                  <img style={{margin:"auto",width:"300px",height:"auto"}} alt="Nft avatar" src={"https://ipfs.io/ipfs/".concat("".concat(resp.data.image).substring(7))}/>
                  <br/>
                <Flex justifyContent="space-between">
                <Button onClick={
                  async ()=>{
                    try {

                      const tx = await wstakingcontract.unstake(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Unstaked'), t('NFT is unstaked from the pool'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Unstake</Button>
                <Button onClick={
                  async()=>{
                    try {

                      const tx = await wstakingcontract.harvest(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Harvested'), t('Reward is harvested and sent to your wallet'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Harvest</Button>
                  
                </Flex>
                
              </FarmCardInnerContainer>
        
              
            </StyledCard>);
          }
          catch(e){
            const resp={nam:"N/A",desc:"N/A",img:"N/A"};
            alldata.push(<StyledCard key={i} isActive={!false}>
              <FarmCardInnerContainer>
                
              <Flex justifyContent="space-between">
                  <Text>NFT Id:</Text>
                  <Text bold>{Number(res[i].tokenId)}</Text>
                </Flex>
               
                <br/>
                <Flex justifyContent="space-between">
                  <Text>Desc:</Text>
                  <Text bold>{resp.desc}</Text>
                </Flex>
                <br/>
                <img style={{margin:"auto",width:"400px",height:"auto"}} alt="Nft avatar" src="/logo.png"/>
                  <br/>
               
                <br/>
                
                <Flex justifyContent="space-between">
                <Button onClick={
                  async ()=>{
                    try {

                      const tx = await wstakingcontract.unstake(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Unstaked'), t('NFT is unstaked from the pool'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Unstake</Button>
                <Button onClick={
                  async()=>{
                    try {
                        console.log(Number(res[i].tokenId))
                      const tx = await wstakingcontract.harvest(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Harvested'), t('Reward is harvested and sent to your wallet'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Harvest</Button>
                </Flex>
              </FarmCardInnerContainer>
            </StyledCard>);
          }
        }


        const nftsids=await contract.walletOfOwner(account);
        const len=nftsids.length;
        
        for(let i=0;i<len;i++)
        {
          
           
            const uri=await contract.tokenURI(nftsids[i]);
            console.log("https://".concat(nftpools[pid].ipfsurl).concat(uri.substring(7)))
            const resp= await axios.get("https://".concat(nftpools[pid].ipfsurl).concat(uri.substring(7)),{
                headers: {
                    'content-type': 'application/json'
                }
            });
            console.log(resp)
            alldata.push(<StyledCard key={i} isActive={!false}>
             
                <FarmCardInnerContainer>
                  
                <Flex justifyContent="space-between">
                    <Text>NFT Id:</Text>
                    <Text bold>{Number(nftsids[i])}</Text>
                  </Flex>
                  <br/>
                  
                  <Flex justifyContent="space-between">
                    <Text>Description:</Text>
                    <Text bold>{resp.data.description}</Text>
                  </Flex>
                  <br/>
                  <img style={{margin:"auto",width:"300px",height:"auto"}} alt="Nft avatar" src={"https://".concat(nftpools[pid].ipfsurl).concat(resp.data.image.substring(7))}/>
                  <br/>
                  <Flex justifyContent="center">
                  <Button  onClick={()=>{
                    hist.push('/stakeModal?pid='.concat(pid).concat('&tid=').concat(nftsids[i]))
                  }}>Stake</Button>
                  
                </Flex>
                </FarmCardInnerContainer>
          
                
              </StyledCard>);
        }
         setMystate(alldata);
      }
    }
    loadData();
  },[])

 

  

  return (
    <>
    
      <PageHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('NFTs')}
        </Heading>
        <Heading scale="lg" color="text">
          {t('View your NFTs')}
        </Heading>
        
      </PageHeader>
      <Page style={{textAlign:"center",display:"flex",flexWrap:"wrap"}}>
      
        {mystate}
        
      </Page>
    </>
  )
}

export default ViewNFTs
