import React, { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import { ethers } from 'ethers'
import { Modal, ModalBody, Text, Image, Button, BalanceInput, Flex } from '@pancakeswap/uikit'
import { PoolIds, Ifo } from 'config/constants/types'
import { useTranslation } from 'contexts/Localization'
import { getBalanceAmount, getDecimalAmount, getFullDisplayBalance } from 'utils/formatBalance'
import useApproveConfirmTransaction from 'hooks/useApproveConfirmTransaction'
import { DEFAULT_TOKEN_DECIMAL } from 'config'
import { useDNFTContract, useERC20,  useNftStakingContract } from 'hooks/useContract'
import { BIG_NINE, BIG_TEN } from 'utils/bigNumber'
import tokens from 'config/constants/tokens'
import { getContract } from 'utils'
import { useGetDragonBalance } from 'hooks/useTokenBalance'
import useToast from 'hooks/useToast'
import { useHistory } from 'react-router-dom'
import { getDNFTContract } from 'utils/contractHelpers'
import nftpools from 'config/constants/nftpools'



  const gasPrice = BIG_TEN.times(BIG_TEN.pow(BIG_NINE)).toString()
const StakingModal: React.FC = () => {
  const hist=useHistory();
  const [value, setValue] = useState('')
  const { account } = useWeb3React()
  const pid = new URLSearchParams(hist.location.search).get('pid');
  const tid = new URLSearchParams(hist.location.search).get('tid');
  console.log(pid," = ",tid)
  const contract=useNftStakingContract(nftpools[pid].contractAddress[56]);
  const usNFT=useDNFTContract(nftpools[pid].stakingToken.address[56]);
  const { toastError, toastSuccess } = useToast()
 
  const [approved, setApproved] = useState(false);
  
  
  console.log(tid)
  const { t } = useTranslation()
  useEffect(()=>{
    async function loaddata() {
      const NFTCont=getDNFTContract(nftpools[pid].stakingToken.address[56]);
      const allowance=await NFTCont.getApproved(tid)
    if(allowance!==nftpools[pid].contractAddress[56])
      setApproved(false)
      else
    setApproved(true)
    }
    
    loaddata();
  },[])

  return (
    
      <div style={{width:"20rem",margin:"5rem",marginLeft:"auto",marginRight:"auto",height:"10rem",backgroundColor:"#300",borderRadius:"2rem",textAlign:"center",padding:"2rem"}}>
      
        <Text color="textSubtle" fontSize="12px" mb="24px">
          {t(
            approved?'Stake your NFT for staking.':'Approve your Nft for staking.',
          )}
        </Text>
        {approved?<Button
       
          onClick={async ()=>{
            try {

            const tx = await contract.stake(tid,{gasPrice,value:"10000000000000000"});
             const receipt=await tx.wait()
              if (receipt.status) {
                toastSuccess(t('Staked'), t('NFT is staked in the pool'))
                hist.goBack();
              }
            } catch (error) {
              toastError(t('Error'), t('You are not allowed.'))
              
            
            }
          }}
        >Stake</Button>:<Button
       
        onClick={async ()=>{
          try {

          const tx = await usNFT.approve(nftpools[pid].contractAddress[56],tid);
           const receipt=await tx.wait()
            if (receipt.status) {
              toastSuccess(t('Approved'), t('NFT is approved for staking'))
              setApproved(true);
            }
          } catch (error) {
            toastError(t('Error'), t('You are not allowed.'))
            
          
          }
        }}
      >Approve</Button>}
       
        
      </div>
  
  )
}

export default StakingModal